
.main-content {
  padding-bottom: 1.1rem;
  -webkit-overflow-scrolling: touch;
}
.main-content::-webkit-scrollbar {
  width: 0;
  height: 0;
}
.m-box {
  background-color: #fff;
  margin-top: 0.2rem;
}
.m-box:nth-child(2) {
  margin-top: 0;
}
.m-box.douban-time {
  background-color: #f0f0f0;
  margin-top: 0;
}
.m-box .title {
  height: 0.8rem;
  line-height: 0.8rem;
  position: relative;
  padding: 0 0.25rem;
  overflow: hidden;
}
.m-box .title .title-lf {
  display: block;
  width: 0.12rem;
  height: 0.4rem;
  position: absolute;
  left: 0;
  top: 0.2rem;
  background-color: #76d49b;
}
.m-box .title h3 {
  display: inline-block;
}
.m-box .title .title-rt {
  display: inline-block;
  float: right;
}
.m-box .content {
  padding: 0 0.25rem;
  padding-top: 0.3rem;
  position: relative;
}
.m-box .content:nth-child(2) {
  padding-top: 0;
}
.m-box .content:last-child:after {
  content: none;
}
.m-box .content:after {
  content: '';
  position: absolute;
  left: 0.25rem;
  bottom: 0;
  width: 7.25rem;
  height: 1px;
  background: #e3e3e3;
}
.m-box .content .content-detail {
  overflow: hidden;
  margin-bottom: 0.3rem;
}
.m-box .content .content-detail .detail-lf {
  width: 5rem;
  float: left;
}
.m-box .content .content-detail .detail-lf.noImg {
  width: 100%;
}
.m-box .content .content-detail .detail-lf h3 {
  font-size: 16px;
  font-weight: 500;
  line-height: 1.41;
  text-align: justify;
  margin-bottom: 0.15rem;
}
.m-box .content .content-detail .detail-lf p {
  display: -webkit-box;
  color: #aaa;
  font-size: 12px;
  line-height: 1.5;
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-align: justify;
}
.m-box .content .content-detail .detail-rt {
  width: 1.73rem;
  height: 1.73rem;
  float: right;
  font-size: 0;
  background: url("../../assets/images/main/detail1.jpg") no-repeat;
  background-size: cover;
}
.m-box .content .content-detail.comment-lf {
  margin-bottom: 0;
}
.m-box .content .content-detail.comment-lf .detail-lf .comment-content {
  padding-top: 0.4rem;
  padding-bottom: 0.3rem;
  font-size: 12px;
  color: #aaa;
  position: relative;
}
.m-box .content .content-detail.comment-lf .detail-lf .comment-content:before {
  content: '';
  width: 0.8rem;
  height: 1px;
  background-color: #ddd;
  position: absolute;
  left: 0;
  top: 0.2rem;
}
.m-box .content .content-detail.comment-lf .detail-rt.comment-rt {
  height: 2.5rem;
}
.m-box .content .author {
  width: 100%;
  overflow: hidden;
  color: #ccc;
  font-size: 12px;
  padding-bottom: 0.3rem;
  padding-top: 0.2rem;
  position: relative;
}
.m-box .content .author:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0.4rem;
  height: 1px;
  background: #e3e3e3;
}
.m-box .content .author span {
  display: inline-block;
}
.m-box .content .author span.author-lf {
  float: left;
}
.m-box .content .author span.author-rt {
  float: right;
}
.m-box .content .scroll-items {
  font-size: 0;
  white-space: nowrap;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  padding-bottom: 0.25rem;
  padding-right: 0.25rem;
}
.m-box .content .scroll-items::-webkit-scrollbar {
  width: 0;
  height: 0;
}
.m-box .content .scroll-items li {
  display: inline-block;
  vertical-align: top;
  margin-left: 0.25rem;
}
.m-box .content .scroll-items li a {
  display: block;
  position: relative;
}
.m-box .content .scroll-items li a .scroll-item-img {
  width: 2rem;
  height: 2rem;
  background: url("../../assets/images/main/detail1.jpg") no-repeat;
  background-size: cover;
  border-radius: 0.06rem;
}
.m-box .content .scroll-items li a .scroll-item-title {
  position: absolute;
  left: 0.2rem;
  bottom: 0.2rem;
  font-size: 12px;
  color: #fff;
}
.m-box .content .scroll-items li a .time-top {
  position: relative;
}
.m-box .content .scroll-items li .time-box {
  width: 5.5rem;
  height: 5.15rem;
  border-radius: 5px;
  overflow: hidden;
  border: 1px solid #dcdcdc;
  background-color: #fff;
}
.m-box .content .scroll-items li .time-box .time-top {
  width: 100%;
  height: 2rem;
  background: url("../../assets/images/main/time1.jpg") no-repeat;
  background-size: cover;
  position: relative;
  font-size: 12px;
  color: #fff;
  padding: 0.2rem 0.3rem;
  box-sizing: border-box;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.m-box .content .scroll-items li .time-box .time-top p {
  margin-bottom: 0.6rem;
}
.m-box .content .scroll-items li .time-box .time-top p span {
  color: #d4d4d4;
}
.m-box .content .scroll-items li .time-box .time-top h3 {
  width: 72%;
  display: -webkit-box;
  line-height: 1.4;
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-align: justify;
  white-space: normal;
}
.m-box .content .scroll-items li .time-box .time-content {
  padding: 0.3rem 0.3rem 0 0.3rem;
}
.m-box .content .scroll-items li .time-box .time-content h3 {
  font-weight: 500;
  font-size: 16px;
  color: #494949;
  margin-bottom: 0.1rem;
}
.m-box .content .scroll-items li .time-box .time-content span {
  font-size: 12px;
  color: #aaa;
}
.m-box .content .scroll-items li .time-box .time-content .author-detail {
  overflow: hidden;
  font-size: 0;
  margin: 0.2rem 0;
}
.m-box .content .scroll-items li .time-box .time-content .author-detail img {
  width: 0.9rem;
  height: 0.9rem;
  display: inline-block;
  float: left;
  border-radius: 50%;
}
.m-box .content .scroll-items li .time-box .time-content .author-detail .author-detail-rt {
  width: 4rem;
  float: left;
  padding-left: 0.3rem;
  padding-top: 0.1rem;
  box-sizing: border-box;
}
.m-box .content .scroll-items li .time-box .time-content .author-detail .author-detail-rt .author-name {
  display: block;
  margin-bottom: 0.2rem;
  color: #333;
}
.m-box .content .scroll-items li .time-box .time-content .author-detail .author-detail-rt .author-intr {
  color: #aaa;
  font-size: 12px;
  text-overflow: ellipsis;
  overflow: hidden;
}
.m-box .content .scroll-items li .time-box .time-button {
  width: 4.9rem;
  height: 0.6rem;
  line-height: 0.6rem;
  margin: 0 auto;
  color: #76d49b;
  border-radius: 0.1rem;
  border: 1px solid #76d49b;
  font-size: 14px;
  text-align: center;
}


