
.mine {
  padding-bottom: 1.1rem;
}
.mine-head {
  display: block;
  width: 100%;
  height: 3rem;
  background: url("../../assets/images/mine/live_player_bg.jpg") no-repeat;
  background-size: cover;
  position: relative;
}
.mine-head img {
  width: 1.3rem;
  height: 1.3rem;
  border-radius: 50%;
  border: 2px solid #fff;
  position: absolute;
  top: 0.7rem;
  left: 0.6rem;
}
.mine-head .loginRegister {
  width: 3rem;
  height: 0.7rem;
  font-size: 16px;
  color: #fff;
  text-align: center;
  line-height: 0.68rem;
  border: 1px solid #fff;
  border-radius: 3px;
  text-decoration: none;
  position: absolute;
  top: 1rem;
  left: 3rem;
}
.mine-head .userInfo {
  width: 5.1rem;
  position: absolute;
  top: 0.75rem;
  right: 0;
  color: #fff;
  font-size: 12px;
}
.mine-head .userInfo h3 {
  margin-bottom: 0.1rem;
  font-size: 16px;
}
.mine-head .userInfo .userInfo-content {
  position: relative;
  overflow: hidden;
  padding-bottom: 0.15rem;
  padding-right: 0.2rem;
  line-height: 18px;
  border-bottom: 1px solid #fff;
}
.mine-head .userInfo .userInfo-content .yourPages {
  float: right;
}
.mine-head .userInfo .userInfo-content .yourPages .fa {
  vertical-align: top;
  color: #fff;
}
.mine-head .userInfo .attentions {
  margin-top: 0.2rem;
}
.mine-head .userInfo .attentions span {
  margin-right: 0.6rem;
}
.mine-head .userInfo .attentions span i {
  font-style: normal;
  margin-left: 0.05rem;
}
.remind-box {
  margin-top: 0.2rem;
  padding-left: 0.2rem;
  background-color: #fff;
}
.remind-box .remind {
  display: block;
  height: 0.8rem;
  line-height: 0.8rem;
  border-bottom: 1px solid #dcdcdc;
  overflow: hidden;
  position: relative;
}
.remind-box .remind img {
  display: inline-block;
  width: 0.5rem;
  vertical-align: middle;
  margin-right: 0.1rem;
}
.remind-box .remind .fa {
  padding: 0.12rem 0.2rem;
  font-size: 18px;
  position: absolute;
  right: 0.1rem;
  top: 0.1rem;
}
.remind-box .remind-bt {
  height: 0.8rem;
  line-height: 0.8rem;
  text-align: center;
  color: #aaa;
  font-size: 12px;
}
.application-box {
  margin: 0.2rem 0;
}
.application-box ul {
  display: -ms-flexbox;
  display: flex;
  height: 2rem;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  background-color: #fff;
  border-bottom: 1px solid #dcdcdc;
}
.application-box ul li a {
  width: 1.6rem;
  display: block;
  text-align: center;
  padding: 0.2rem 0;
}
.application-box ul li a .app-img {
  display: block;
  margin-bottom: 0.2rem;
}
.application-box ul li a .app-img img {
  width: 0.6rem;
}


