
.header {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 50px;
  -ms-flex-align: center;
      align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #76d49b;
  z-index: 1;
}
.header.bg-white {
  background-color: #fff;
}
.header .header-title {
  -ms-flex: 2;
      flex: 2;
  font-size: 18px;
  font-weight: 500;
  text-align: center;
  color: #fff;
}
.header .header-box {
  -ms-flex: 1;
      flex: 1;
  color: #fff;
  font-size: 16px;
}
.header .header-box a {
  display: block;
  padding: 0.1rem 0.3rem;
}
.header .header-box a .share {
  width: 0.4rem;
}
.header .header-box.header-lf {
  text-align: left;
}
.header .header-box.header-rt {
  text-align: right;
}
.header .header-box .city span {
  font-size: 14px;
  color: #333;
}
.header .header-box .city img {
  width: 0.4rem;
  display: inline-block;
  vertical-align: middle;
}


