
.scroller {
  background-color: #fff;
  margin-top: 0.2rem;
}
.scroller.douban-time {
  background-color: #f0f0f0;
  margin-top: 0;
}
.scroller .head {
  height: 0.8rem;
  line-height: 0.8rem;
  position: relative;
  padding: 0 0.25rem;
  overflow: hidden;
}
.scroller .head .head-lf {
  display: block;
  width: 0.12rem;
  height: 0.4rem;
  position: absolute;
  left: 0;
  top: 0.2rem;
  background-color: #76d49b;
}
.scroller .head .head-title {
  display: inline-block;
}
.scroller .head .head-rt {
  display: inline-block;
  float: right;
  color: #42bd56;
}
.scroller .head .head-rt .fa {
  color: #42bd56;
}
.scroller .content {
  padding: 0.1rem 0;
}
.scroller .content .scroll-items {
  font-size: 0;
  white-space: nowrap;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  padding-bottom: 0.25rem;
  padding-right: 0.25rem;
}
.scroller .content .scroll-items::-webkit-scrollbar {
  width: 0;
  height: 0;
}
.scroller .content .scroll-items li {
  display: inline-block;
  vertical-align: top;
  margin-left: 0.25rem;
}
.scroller .content .scroll-items li .movie-box {
  display: block;
  width: 2rem;
  position: relative;
  text-decoration: none;
}
.scroller .content .scroll-items li .movie-box .item-poster {
  width: 100%;
  height: 2.85rem;
  background: url("../pages/video/movie/images/movie.jpg");
  background-repeat: no-repeat !important;
  background-size: cover !important;
  overflow: hidden;
  position: relative;
}
.scroller .content .scroll-items li .movie-box .item-title {
  max-width: 100%;
  display: block;
  margin-top: 0.15rem;
  font-size: 14px;
  font-weight: 500;
  color: #111;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
}
.scroller .content .scroll-items li .movie-box .star-name {
  font-size: 12px;
  color: #aaa;
  margin-top: 0.1rem;
  margin-bottom: -0.05rem;
}
.scroller .content .scroll-items li .movie-box .star-name span {
  font-size: 12px;
  vertical-align: middle;
}
.scroller .content .scroll-items li .movie-box .price {
  max-width: 100%;
  display: block;
  margin-top: 0.15rem;
  font-size: 14px;
  font-weight: 500;
  color: #e76648;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
}
.scroller .content .scroll-items li .movie-box .author {
  max-width: 100%;
  display: block;
  margin-top: 0.15rem;
  font-size: 14px;
  font-weight: 500;
  color: #aaa;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
}
.scroller .content .scroll-items li .movie-box .author.city-author {
  font-size: 12px;
  margin-top: 0.1rem;
}
.scroller .content .scroll-items li .topTen-box {
  width: 3.2rem;
  height: 2.7rem;
  text-align: center;
  padding-top: 0.5rem;
  border-radius: 5px;
  background: -moz-linear-gradient(top, #deb887, #f0f0f0);
  background: -webkit-gradient(linear, 0 0, 0 bottom, from(#deb887), to(#f0f0f0));
  color: #fff;
}
.scroller .content .scroll-items li .topTen-box h3 {
  font-size: 16px;
  margin-bottom: 0.1rem;
}
.scroller .content .scroll-items li .topTen-box p {
  font-size: 12px;
  margin-bottom: 0.3rem;
}
.scroller .content .scroll-items li .topTen-box .img-box {
  font-size: 0;
}
.scroller .content .scroll-items li .topTen-box .img-box img {
  display: inline-block;
  vertical-align: middle;
  width: 0.7rem;
}
.scroller .content .scroll-items li .topTen-box .img-box img:nth-child(2) {
  width: 1rem;
}
.scroller .content .scroll-items li:nth-child(2) .topTen-box {
  background: -moz-linear-gradient(top, #15a216, #f0f0f0);
  background: -webkit-gradient(linear, 0 0, 0 bottom, from(#15a216), to(#f0f0f0));
}
.scroller .content .scroll-items li:nth-child(3) .topTen-box {
  background: -moz-linear-gradient(top, #9770b5, #f0f0f0);
  background: -webkit-gradient(linear, 0 0, 0 bottom, from(#9770b5), to(#f0f0f0));
}
.scroller .content .scroll-items li:nth-child(4) .topTen-box {
  background: -moz-linear-gradient(top, #f78484, #f0f0f0);
  background: -webkit-gradient(linear, 0 0, 0 bottom, from(#f78484), to(#f0f0f0));
}
.marginLeft3 {
  margin-left: 0.3rem !important;
}
.marginLeft3 .movie-box {
  width: 2.1rem !important;
}


