
.mint-swipe.banner {
  height: 3rem;
}
.mint-swipe.banner .mint-swipe-item .banner-item {
  display: block;
}
.mint-swipe.banner .mint-swipe-item .banner-item img {
  width: 100%;
  height: 3rem;
}
.mint-swipe.banner .mint-swipe-indicators {
  padding: 0 0.1rem 0.03rem 0.1rem;
  height: 0.15rem;
  line-height: 0.15rem;
  background: rgba(0,0,0,0.4);
  border-radius: 0.1rem;
  bottom: 6px;
}
.mint-swipe.banner .mint-swipe-indicators .mint-swipe-indicator {
  width: 0.1rem;
  height: 0.1rem;
  background-color: #fff;
  border-radius: 50%;
  margin-right: 0.1rem;
  opacity: 0.6;
  vertical-align: middle;
}
.mint-swipe.banner .mint-swipe-indicators .mint-swipe-indicator.is-active {
  background-color: #76d49b;
  opacity: 1;
}


