
.bookLabel {
  width: 0.3rem;
  height: 0.4rem;
  padding-top: 0.1rem;
  background-color: #42bd56;
  text-align: center;
  color: #fff;
  font-size: 12px;
  position: absolute;
  left: 0.15rem;
  top: 0.04rem;
}
.contentAfterN .content:after {
  content: none !important;
}
.content .storeBook {
  display: block;
  overflow: hidden;
  margin: 0.2rem 0;
}
.content .storeBook .storeBook-lf {
  width: 2rem;
  height: 2.64rem;
  background-size: cover !important;
  float: left;
  position: relative;
  margin-right: 0.3rem;
}
.content .storeBook .storeBook-rt {
  overflow: hidden;
  padding-top: 0.2rem;
}
.content .storeBook .storeBook-rt .storeBook-content {
  overflow: hidden;
}
.content .storeBook .storeBook-rt .storeBook-content .storeBook-title {
  margin: 0.2rem 0;
}
.content .storeBook .storeBook-rt .storeBook-content .storeBook-title .storeBook-price {
  float: right;
  font-size: 14px;
  color: #e76648;
}
.content .storeBook .storeBook-rt .storeBook-content .storeBook-title .storeBook-name {
  font-size: 16px;
  font-weight: 500;
  color: #111;
  margin-right: 0.8rem;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
}
.content .storeBook .storeBook-rt .storeBook-content .storeBook-title .storeBook-author {
  display: block;
  margin-top: 0.1rem;
  font-size: 13px;
  color: #aaa;
}
.content .storeBook .storeBook-rt .storeBook-content .storeBook-info {
  display: -webkit-box;
  overflow: hidden;
  text-align: justify;
  -webkit-line-clamp: 4;
  font-size: 13px;
  font-weight: 300;
  line-height: 1.5;
  color: #9b9b9b;
}
.lookMore {
  display: block;
  width: 100%;
  height: 0.6rem;
  line-height: 0.6rem;
  padding-bottom: 0.3rem;
  font-size: 14px;
  color: #42bd56;
  text-align: center;
}


