
.movieInfo {
  display: block;
  padding: 0.3rem 0.25rem;
  font-size: 0;
  position: relative;
  border-bottom: 1px solid #eee;
}
.movieInfo .movieInfo-img {
  display: inline-block;
  width: 1.6rem;
  margin-right: 0.25rem;
  vertical-align: top;
}
.movieInfo .middle {
  display: inline-block;
  width: 3.5rem;
  font-size: 12px;
}
.movieInfo .middle h3 {
  font-size: 16px;
  font-weight: bold;
  color: #111;
  line-height: 1.5;
  max-width: 4rem;
}
.movieInfo .middle .cityDetail-p {
  margin-top: 0.1rem;
}
.movieInfo .middle .cityDetail-p span {
  color: #111;
  font-size: 14px;
  vertical-align: middle;
}
.movieInfo .middle p {
  color: #aaa;
  text-align: justify;
  line-height: 1.5;
}
.movieInfo .middle .peopleLook {
  font-size: 14px;
  color: #111;
  margin-top: 0.1rem;
}
.movieInfo .movieInfo-btn {
  display: block;
  width: 0.8rem;
  height: 0.5rem;
  line-height: 0.5rem;
  border-radius: 3px;
  border: 1px solid #e76648;
  font-size: 14px;
  color: #e76648;
  text-align: center;
  background-color: #fff;
  position: absolute;
  top: 1.1rem;
  right: 0.25rem;
  outline: none;
}


