
.group {
  background-color: #fff;
  padding-bottom: 1.1rem;
}
.group-head {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 3rem;
  background: url("../../assets/images/group/ic_group_banner.png") no-repeat;
  background-size: cover;
  position: relative;
  text-align: center;
}
.group-head .group-head-box h3 {
  font-size: 20px;
  font-weight: 500;
  color: #111;
  margin-bottom: 0.2rem;
}
.group-head .group-head-box p {
  color: #676767;
  font-size: 14px;
}
.group-head span {
  display: block;
  position: absolute;
  bottom: 0.3rem;
  left: 0.2rem;
  font-size: 12px;
  color: #676767;
}
.recommend {
  width: 7.1rem;
  height: 1.3rem;
  margin: 0.2rem;
  position: relative;
  background: url("../../assets/images/group/ic_rec_group_banner_6.png") no-repeat;
  background-size: cover;
  border-radius: 3px;
}
.recommend span {
  display: block;
  font-size: 13px;
  color: #fff;
  position: absolute;
  left: 0.2rem;
  bottom: 0.15rem;
}
.group-item {
  padding: 0.2rem 0;
  margin: 0 0.2rem;
  border-top: 1px solid #dcdcdc;
  position: relative;
}
.group-item .group-item-title {
  width: 1.5rem;
  text-align: center;
  font-size: 14px;
  position: absolute;
  top: -0.12rem;
  left: 2.8rem;
  background-color: #fff;
}
.group-item ul li {
  padding: 0.2rem 0;
}
.group-item ul li .group-item-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
  position: relative;
}
.group-item ul li .group-item-list .group-item-listHead {
  width: 1rem;
  height: 1rem;
  display: inline-block;
  border-radius: 3px;
  margin-right: 0.2rem;
}
.group-item ul li .group-item-list .group-item-listContent {
  max-width: 4.2rem;
}
.group-item ul li .group-item-list .group-item-listContent h3 {
  font-size: 16px;
  font-weight: 500;
  color: #111;
  margin-bottom: 0.12rem;
}
.group-item ul li .group-item-list .group-item-listContent p {
  display: -webkit-box;
  font-size: 12px;
  line-height: 1.2;
  color: #676767;
  -webkit-line-clamp: 2;
  text-overflow: ellipsis;
  overflow: hidden;
}
.group-item ul li .group-item-list .group-item-listRight {
  min-width: 1.8rem;
  text-align: right;
  position: absolute;
  right: 0;
}
.group-item ul li .group-item-list .group-item-listRight span {
  color: #aaa;
  vertical-align: middle;
}
.group-item ul li .group-item-list .group-item-listRight .check {
  display: inline-block;
  width: 0.4rem;
  height: 0.4rem;
  vertical-align: middle;
  background: url("../../assets/images/group/ic_group_check_anonymous.png") no-repeat;
  background-size: cover;
}
.group-item ul li .group-item-list .group-item-listRight .check.checked {
  background: url("../../assets/images/group/ic_group_checked_anonymous.png") no-repeat;
  background-size: cover;
}


