
.movieToplist-content {
  padding-top: 50px;
  background-color: #fff;
}
.movieCharts {
  padding-bottom: 1rem;
}
.movieCharts:first-child {
  padding-top: 1rem;
}
.movieCharts .line {
  width: 3rem;
  height: 1px;
  margin: 0 auto;
  background-color: #dcdcdc;
  position: relative;
}
.movieCharts .line h3 {
  width: 0.4rem;
  text-align: center;
  position: absolute;
  top: -0.1rem;
  left: 1.3rem;
  color: #f00;
  font-weight: bold;
  font-style: oblique;
  background-color: #fff;
}
.movieCharts .content {
  display: block;
  width: 6.9rem;
  height: 2.55rem;
  margin-left: 0.3rem;
  margin-top: 0.5rem;
  border: 1px solid #dcdcdc;
  border-radius: 3px;
  padding: 0.3rem;
  padding-left: 2rem;
  box-sizing: border-box;
  position: relative;
}
.movieCharts .content img {
  display: block;
  width: 1.4rem;
  height: 1.95rem;
  position: absolute;
  left: 0.3rem;
  top: 0.3rem;
}
.movieCharts .content h3 {
  font-weight: bold;
  font-size: 16px;
  color: #111;
  margin-bottom: 0.2rem;
}
.movieCharts .content p {
  color: #676767;
  line-height: 1.5;
  font-size: 12px;
}
.movieCharts .content .score {
  color: #676767;
}


