
.gb-head {
  width: 4.8rem;
  margin: 0 auto;
  padding: 0.4rem 0;
  font-size: 0;
  overflow: hidden;
}
.gb-head img {
  width: 1.5rem;
  height: 1.5rem;
  float: left;
  margin-right: 0.4rem;
}
.gb-head h3 {
  font-size: 14px;
