}
.gb-content ul li .gb-item {
  display: block;
  position: relative;
  padding: 0.8rem 0.6rem 0.8rem 1.5rem;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0 1px 1px #ddd, 0 1px 1px #ddd, 0 0 0 #ddd, 0 2px 2px #ddd;
}
.gb-content ul li .gb-item .gb-item-headPic {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  display: block;
  position: absolute;
