}
.gb-content ul li .gb-item .gb-item-content {
  position: relative;
}
.gb-content ul li .gb-item .gb-item-content .content-attention {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  background-color: #42bd56;
  color: #fff;
  font-size: 13px;
  padding: 0.1rem 0.4rem;
  border-radius: 3px;
  border: none;
  outline: none;
  cursor: pointer;
}
.gb-content ul li .gb-item .gb-item-content .userName {
  font-weight: 500;
  font-size: 16px;
  margin-bottom: 0.1rem;
  color: #111;
}
.gb-content ul li .gb-item .gb-item-content .attention-people {
  display: block;
  font-size: 12px;
  color: #aaa;
