}
.gb-content ul li .gb-item .gb-item-content .publish-mood {
  font-size: 13px;
  line-height: 1.5;
  color: #111;
  margin-bottom: 0.3rem;
  text-align: justify;
}
.gb-content ul li .gb-item .gb-item-content .publish-imgs {
  width: 100%;
  height: 3rem;
  display: -ms-flexbox;
