}
.gb-content ul li .gb-item .gb-item-content .publish-imgs .publish-img-rt .publish-img:nth-child(2) {
  margin-top: 0.04rem;
}
.gb-content ul li .gb-item .gb-item-content .publish-imgs .publish-img-rt .publish-more {
  display: block;
