
.login {
  height: 12.38rem;
  background-color: #fff;
  padding-top: 0.96rem;
}
.welcome {
  height: 3rem;
  line-height: 3rem;
  text-align: center;
  font-size: 30px;
  color: #42bd56;
  font-weight: 500;
}
.form {
  padding: 0 0.4rem;
}
.form input {
  width: 100%;
  display: block;
  padding: 0.1rem 0.2rem;
  border: none;
  outline: none;
  font-size: 15px;
  position: relative;
  box-sizing: border-box;
  border-bottom: 1px solid #dcdcdc;
}
.form input#userName {
  margin-bottom: 0.6rem;
}
.form input:focus {
  color: #42bd56;
}
.login-btn {
  width: 6.8rem;
  height: 0.8rem;
  margin-left: 0.35rem;
  margin-top: 0.4rem;
  border: none;
  border-radius: 3px;
  outline: none;
  background-color: #42bd56;
  color: #fff;
  font-size: 15px;
}
.forget-box {
  display: -ms-flexbox;
  display: flex;
  margin: 0 auto;
  margin-top: 0.4rem;
  width: 4rem;
  font-size: 14px;
  color: #aaa;
}
.forget-box a {
  display: block;
  width: 2rem;
  text-align: center;
  position: relative;
}
.forget-box a.colorGreen {
  color: #42bd56;
}
.forget-box a.colorGreen:after {
  content: '';
  width: 1px;
  height: 15px;
  background-color: #aaa;
  position: absolute;
  right: 0;
  top: 0;
}


