
.register {
  height: 12.38rem;
  background-color: #fff;
  padding-top: 0.96rem;
}
.register .welcome {
  height: 2rem;
  line-height: 2rem;
}
.register .re-form {
  width: 6.8rem;
  margin: 0 auto;
  border: 1px solid #dcdcdc;
  border-radius: 5px;
}
.register .re-form li {
  display: block;
  padding: 2px 0;
}
.register .re-form li:last-child input {
  border-bottom: 0;
}
.register .re-form li input {
  width: 100%;
  display: block;
  box-sizing: border-box;
  outline: none;
  padding: 0.2rem;
  padding-right: 1.5rem;
  font-size: 15px;
  border: none;
  border-bottom: 1px solid #dcdcdc;
}
.register .login-btn {
  margin-top: 0.2rem;
}
.register .agreement {
  margin-top: 0.3rem;
  text-align: center;
  color: #aaa;
}
.register .agreement a {
  text-decoration: underline;
}


