  display: flex;
  height: 0.5rem;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
}
.broadcastDetail .content .detail .detail-price .price-lf {
  display: -ms-flexbox;
  display: flex;
  width: 1rem;
  -ms-flex-align: center;
      align-items: center;
  font-size: 12px;
  color: #aaa;
  border: 1px solid #eee;
  border-radius: 3px;
  padding: 0 0.1rem;
  box-sizing: border-box;
}
.broadcastDetail .content .detail .detail-price .price-lf img {
  display: inline-block;
