}
.broadcastDetail .content .detail .detail-price .price-people ul li img {
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 50%;
}
.broadcastDetail .content .detail .detail-price .price-people span {
  margin-left: 0.1rem;
  margin-right: 0.2rem;
}
.broadcastDetail .content .comment {
  background-color: #fff;
