
.movie-box.tv-box {
  width: 6rem !important;
  padding: 0.2rem;
  border: 1px solid #dcdcdc;
  overflow: hidden;
  border-radius: 5px;
}
.movie-box.tv-box .tv-left {
  width: 1.6rem !important;
  height: 2.1rem !important;
  float: left;
  margin-right: 0.3rem;
}
.movie-box.tv-box .tv-right {
  float: left;
  width: 4rem;
}
.movie-box.tv-box .tv-right .tv-title {
  font-size: 18px;
  color: #111;
  margin: 0.1rem 0 !important;
}
.movie-box.tv-box .tv-right .rating .noScore {
  font-size: 12px;
  display: block;
  margin-top: 0.1rem;
  margin-bottom: 0.05rem;
}
.movie-box.tv-box .tv-right .tv-update {
  display: -webkit-box;
  font-size: 12px;
  color: #aaa;
  margin-bottom: 0.15rem;
  line-height: 1.5;
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-align: justify;
  white-space: normal;
  text-overflow: ellipsis;
}
.movie-box.tv-box .tv-right .tv-look {
  font-size: 12px;
  color: #aaa;
}


