
.movieMore-head {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  position: fixed;
  top: 50px;
  left: 0;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #dcdcdc;
  background-color: #fff;
  z-index: 1;
}
.movieMore-head a {
  line-height: 40px;
  -ms-flex: 1;
      flex: 1;
  text-align: center;
  color: #9c9c9c;
  outline: none;
}
.movieMore-head a.active {
  color: #333;
  border-bottom: 2px solid #9c9c9c;
}
.movieMore {
  background-color: #fff;
}
.movieMore .content {
  width: 15rem;
  overflow: hidden;
  padding-top: 90px;
}
.movieMore-content {
  width: 7.5rem;
  display: inline-block;
  float: left;
  transition: all 0.3s ease-in;
  transform: translateX(0);
}
.movieMore-content:nth-child(2) {
  transform: translateX(-7.5rem);
}
.activeContentLeft {
  transform: translateX(-7.5rem);
}
.activeContentRight {
  transform: translateX(7.5rem) !important;
/*即将上映*/
}
.movieMore .filter {
  width: 100%;
  height: 40px;
  position: fixed;
  top: 92px;
  background-color: #fff;
  overflow: hidden;
  color: #aaa;
  z-index: 1;
}
.movieMore .filter .active {
  color: #333;
}
.movieMore .filter .filter-lf {
  display: -ms-flexbox;
  display: flex;
  width: 4rem;
  height: 40px;
  float: left;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.movieMore .filter .filter-rt {
  display: -ms-flexbox;
  display: flex;
  width: 2rem;
  height: 40px;
  float: right;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  position: relative;
}
.movieMore .filter .filter-rt:before {
  content: '';
  width: 1px;
  height: 26px;
  background-color: #dcdcdc;
  position: absolute;
  top: 7px;
  left: -7px;
}
.movieMore .time {
  width: 100%;
  height: 30px;
  line-height: 30px;
  position: fixed;
  top: 132px;
  background-color: #f0f0f0;
  padding-left: 0.3rem;
  z-index: 1;
}


