
.fictionItem .bookBox {
  display: block;
  margin-top: 0.8rem;
  padding: 0.25rem;
  border: 1px solid #dcdcdc;
  border-radius: 3px;
  font-size: 0;
}
.fictionItem .bookBox .bookBox-lf {
  display: inline-block;
  width: 1.6rem;
  vertical-align: top;
  margin-right: 0.25rem;
}
.fictionItem .bookBox .bookBox-rt {
  display: inline-block;
  font-size: 12px;
}
.fictionItem .bookBox .bookBox-rt h3 {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 0.2rem;
  max-width: 4rem;
  color: #111;
}
.fictionItem .bookBox .bookBox-rt p {
  max-width: 4.5rem;
  text-align: justify;
  line-height: 1.5;
  color: #888;
}
.fictionItem .summary {
  margin-top: 0.4rem;
  text-align: justify;
  line-height: 1.5;
}


