
.m-box .chooseCity {
  font-size: 0;
}
.m-box .chooseCity .chooseCity-item {
  display: inline-block;
  width: 3.15rem;
  padding: 0.2rem 0.3rem;
  font-size: 14px;
  position: relative;
  color: #aaa;
}
.m-box .chooseCity .chooseCity-item:first-child:before {
  content: '';
  width: 1px;
  background-color: #dcdcdc;
  height: 0.68rem;
  position: absolute;
  right: 0;
  top: 0;
}
.m-box .chooseCity .chooseCity-item span {
  color: #42bd56;
}
.m-box .chooseCity .chooseCity-item i {
  position: absolute;
  right: 0.3rem;
}
.city-box {
  padding-top: 1rem !important;
  height: 2rem !important;
  background: url("../../../assets/images/main/time1.jpg") no-repeat !important;
  background-size: cover;
}
.city-box h3 {
  margin-bottom: 0.3rem !important;
}


