
.movie .fa-angle-right {
  color: #42bd56;
}
.m-box .content .scroll-items li .movie-box {
  width: 2rem;
  text-decoration: none;
}
.m-box .content .scroll-items li .movie-box .item-poster {
  width: 100%;
  height: 2.85rem;
  background: url("./images/movie.jpg");
  background-repeat: no-repeat !important;
  background-size: cover !important;
  overflow: hidden;
  position: relative;
}
.m-box .content .scroll-items li .movie-box .item-title {
  max-width: 100%;
  display: block;
  margin-top: 0.15rem;
  font-size: 14px;
  font-weight: 500;
  color: #111;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
}
.m-box .content .scroll-items li .movie-box .star-name {
  font-size: 12px;
  color: #aaa;
  margin-top: 0.1rem;
  margin-bottom: -0.05rem;
}
.m-box .content .scroll-items li .movie-box .star-name span {
  font-size: 12px;
  vertical-align: middle;
}
.m-box .content .scroll-items li .topTen-box {
  width: 3.2rem;
  height: 2.7rem;
  text-align: center;
  padding-top: 0.5rem;
  border-radius: 5px;
  background: -moz-linear-gradient(top, #deb887, #f0f0f0);
  background: -webkit-gradient(linear, 0 0, 0 bottom, from(#deb887), to(#f0f0f0));
  color: #fff;
}
.m-box .content .scroll-items li .topTen-box h3 {
  font-size: 16px;
  margin-bottom: 0.1rem;
}
.m-box .content .scroll-items li .topTen-box p {
  font-size: 12px;
  margin-bottom: 0.3rem;
}
.m-box .content .scroll-items li .topTen-box .img-box {
  font-size: 0;
}
.m-box .content .scroll-items li .topTen-box .img-box img {
  display: inline-block;
  vertical-align: middle;
  width: 0.7rem;
}
.m-box .content .scroll-items li .topTen-box .img-box img:nth-child(2) {
  width: 1rem;
}
.m-box .content .scroll-items li:nth-child(2) .topTen-box {
  background: -moz-linear-gradient(top, #15a216, #f0f0f0);
  background: -webkit-gradient(linear, 0 0, 0 bottom, from(#15a216), to(#f0f0f0));
}
.m-box .content .scroll-items li:nth-child(3) .topTen-box {
  background: -moz-linear-gradient(top, #9770b5, #f0f0f0);
  background: -webkit-gradient(linear, 0 0, 0 bottom, from(#9770b5), to(#f0f0f0));
}
.m-box .content .scroll-items li:nth-child(4) .topTen-box {
  background: -moz-linear-gradient(top, #f78484, #f0f0f0);
  background: -webkit-gradient(linear, 0 0, 0 bottom, from(#f78484), to(#f0f0f0));
}
.m-box.beInterested .content:after {
  content: none;
}
.m-box.beInterested .content .scroll-items li {
  margin-left: 0.3rem;
}
.m-box.beInterested .content .scroll-items li .movie-box {
  width: 2.1rem;
}
.m-box.beInterested .seeMore {
  width: 100%;
  height: 0.6rem;
  line-height: 0.6rem;
  padding-bottom: 0.3rem;
  font-size: 14px;
  color: #42bd56;
  text-align: center;
}
.m-box .content .filter {
  padding-top: 0.2rem;
  border-bottom: 1px solid #ececec;
}
.m-box .content .filter:last-child {
  border-bottom: none;
}
.m-box .content .filter .filter-item {
  display: inline-block;
  margin-right: 0.2rem;
  margin-bottom: 0.2rem;
}
.m-box .content .filter .filter-item a {
  display: block;
  padding: 0.1rem 0.2rem;
  background-color: #fff;
  border: 1px solid #42bd56;
  border-radius: 0.3rem;
  color: #42bd56;
}


