
.fictionMore .movieInfo .middle {
  width: 5.15rem;
}
.movieInfo .comment-box {
  padding: 0.2rem;
  margin-top: 0.3rem;
  background-color: #f0f0f0;
  border-radius: 3px;
}
.movieInfo .comment-box .comment {
  display: -webkit-box;
  -webkit-line-clamp: 4;
  font-size: 14px;
  line-height: 1.5;
  overflow: hidden;
  text-overflow: ellipsis;
}


