
.interestedItem {
  margin-top: 0.2rem;
  background-color: #fff;
}
.interestedItem .movieInfo {
  position: relative;
  border: none;
}
.interestedItem .movieInfo .middle {
  max-width: 4.5rem;
}
.interestedItem .movieInfo .interestedClose {
  position: absolute;
  right: 0.2rem;
  top: 0.2rem;
}
.interestedItem .movieInfo .interestedClose img {
  width: 0.5rem;
}
.interestedItem .watch {
  display: -ms-flexbox;
  display: flex;
  height: 0.8rem;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 0 0.25rem;
  border-top: 1px solid #dcdcdc;
  font-size: 12px;
}
.interestedItem .watch .watch-btn {
  display: block;
  padding: 0.13rem 0.25rem;
  color: #ef7932;
  border: 1px solid #ef7932;
  border-radius: 3px;
}


