
.movieDetail {
  padding-top: 50px;
  background-color: #f9f9f9;
}
.movieDetail .m-box {
  background-color: #f9f9f9 !important;
  padding: 0 0.3rem;
}
.movieDetail .m-box .movieDetail-title {
  color: #aaa;
  font-size: 12px;
  margin-bottom: 0.2rem;
}
.movieDetail .m-box .content {
  padding: 0 !important;
}
.movieDetail .m-box .content .scroll-items li:first-child {
  margin-left: 0 !important;
}
.movieDetail .m-box .content .scroll-items li .movie-box {
  width: 1.5rem !important;
}
.movieDetail .m-box .content .scroll-items li .movie-box .item-poster {
  height: 2rem;
  background: url("../../../../assets/images/main/time1.jpg");
}
.movieDetail .m-box.movieDetail-stills .content .scroll-items li:first-child {
  margin-left: 0 !important;
}
.movieDetail .m-box.movieDetail-stills .content .scroll-items li .movie-box {
  width: 4rem !important;
}
.movieDetail .m-box.movieDetail-stills .content .scroll-items li .movie-box .item-poster {
  height: 3rem !important;
  background: url("../../../../assets/images/main/time1.jpg");
}
.movieDetail-ImgBox {
  display: -ms-flexbox;
  display: flex;
  height: 6rem;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: #333030;
}
.movieDetail-ImgBox img {
  width: 3.6rem;
  height: 5rem;
}
.movieDetail-detail {
  padding: 0.3rem;
  position: relative;
}
.movieDetail-detail .detail-score {
  display: block;
  padding: 0.2rem 0.3rem;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(86,86,86,0.5);
  text-align: center;
  position: absolute;
  top: 0.3rem;
  right: 0.3rem;
}
.movieDetail-detail .detail-score h3 {
  color: #aaa;
  font-size: 12px;
}
.movieDetail-detail .detail-score .score {
  display: block;
  font-size: 20px;
  font-weight: 500;
  color: #111;
  margin-top: 0.1rem;
}
.movieDetail-detail .detail-score .item-rating {
  margin-top: 0;
  margin-bottom: 0.1rem;
}
.movieDetail-detail .detail-score .item-rating .rank {
  text-align: center;
}
.movieDetail-detail .detail-score .score-people {
  color: #6d6d6d;
  font-size: 12px;
}
.movieDetail-detail .movieDetail-title {
  color: #111;
  font-weight: 500;
  font-size: 24px;
  margin-bottom: 0.2rem;
  line-height: 1.5;
  max-width: 5rem;
}
.movieDetail-detail p {
  max-width: 5rem;
  text-align: justify;
  line-height: 1.5;
  font-size: 12px;
  color: #aaa;
  margin-top: 0.05rem;
}
.movieDetail-btn {
  padding: 0.3rem;
  font-size: 0;
}
.movieDetail-btn button {
  outline: none;
  border: 1px solid #ffaf37;
  border-radius: 5px;
  padding: 0.15rem;
  background-color: #f9f9f9;
  font-size: 14px;
  color: #ffaf37;
}
.movieDetail-btn button:first-child {
  width: 2rem;
  margin-right: 0.15rem;
}
.movieDetail-btn button:nth-child(2) {
  width: 4.75rem;
}
.item-head {
  height: 1rem;
  padding: 0 0.3rem;
  line-height: 1rem;
  position: relative;
  border-bottom: 1px solid #ececec;
}
.item-head h3 {
  color: #111;
  font-size: 16px;
  font-weight: 500;
}
.item-head h3 img {
  width: 0.4rem;
  vertical-align: middle;
  margin-right: 0.1rem;
}
.item-head .head-rt {
  display: block;
  position: absolute;
  top: 0;
  right: 0.3rem;
  font-size: 14px;
}
.item-head .head-rt span {
  margin-right: 0.06rem;
}
.item-head .comments-title {
  color: #aaa;
}
.item-head .head-rt.btn {
  height: 0.4rem;
  outline: none;
  color: #42bd56;
  border: 1px solid #42bd56;
  border-radius: 3px;
  font-size: 12px;
  background-color: #fff;
  position: absolute;
  top: 0.3rem;
  right: 0.3rem;
}
.movie-introduction {
  padding: 0.3rem;
}
.movie-introduction h3 {
  font-size: 12px;
  font-weight: normal;
  color: #aaa;
  margin: 0.2rem 0;
}
.movie-introduction .intro-content p {
  display: -webkit-box;
  font-size: 14px;
  line-height: 1.5;
  overflow: hidden;
  text-align: justify;
  text-overflow: ellipsis;
}
.movie-introduction .intro-content p.clamp4 {
  -webkit-line-clamp: 4;
}
.movie-introduction .intro-content .open-intro {
  display: block;
  margin-top: 0.1rem;
  color: #42bd56;
}
.comments-box {
  padding-left: 1.1rem;
  padding-right: 0.3rem;
  position: relative;
}
.comments-box .userImg {
  display: block;
  position: absolute;
  left: 0.3rem;
  top: 0;
}
.comments-box .userImg img {
  width: 0.6rem;
  height: 0.6rem;
  border-radius: 50%;
}
.comments-box .comments-rt-top {
  padding-top: 0.1rem;
  overflow: hidden;
}
.comments-box .comments-rt-top .userName {
  color: #111;
  font-size: 15px;
  font-weight: 500;
}
.comments-box .comments-rt-top .praise {
  float: right;
  color: #aaa;
}
.comments-box .comments-rt-top .praise i {
  margin-right: 0.1rem;
}
.comments-box .comments-rt-content {
  margin: 0.2rem 0;
  text-align: justify;
  line-height: 1.5;
}
.comments-box .comments-rt-time {
  color: #aaa;
  font-size: 12px;
  padding-bottom: 0.4rem;
}
.all-comments {
  display: block;
  height: 0.8rem;
  line-height: 0.8rem;
  font-size: 14px;
  color: #42bd56;
  border-bottom: 1px solid #dcdcdc;
  text-align: center;
}
.all-comments .fa-angle-right {
  color: #42bd56;
}


