
.movieSearch .movieSearch-content {
  padding-top: 0.96rem;
}
.movieSearch-filter {
  height: 0.8rem;
  -ms-flex-align: center;
      align-items: center;
  background-color: #fff;
  border-top: 1px solid #dcdcdc;
  overflow: hidden;
}
.movieSearch-filter .filter-lf {
  display: inline-block;
  width: 1.2rem;
  float: left;
  line-height: 0.8rem;
  text-align: center;
  color: #aaa;
}
.movieSearch-filter .filter-rt {
  display: inline-block;
  width: 6.3rem;
  float: left;
  overflow: hidden;
}
.movieSearch-filter .filter-rt ul {
  height: 0.8rem;
  white-space: nowrap;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}
.movieSearch-filter .filter-rt ul::-webkit-scrollbar {
  width: 0;
  height: 0;
}
.movieSearch-filter .filter-rt ul li {
  display: inline-block;
  line-height: 0.8rem;
  margin-left: 0.4rem;
}
.movieSearch-filter .filter-rt ul li:first-child {
  margin-left: 0rem;
}
.movieSearch-filter .filter-rt ul li:last-child {
  margin-right: 0.2rem;
}
.movieSearch-result {
  background-color: #fff;
  margin-top: 0.2rem;
}
.movieSearch-result .movieSearch-item {
  display: block;
  padding: 0.4rem 0.3rem;
  border-bottom: 1px solid #ececec;
  overflow: hidden;
}
.movieSearch-result .movieSearch-item .movieSearch-item-lf {
  display: inline-block;
  float: left;
  width: 1.6rem;
  margin-right: 0.3rem;
}
.movieSearch-result .movieSearch-item .movieSearch-item-rt {
  width: 5rem;
  display: inline-block;
  float: left;
  padding-top: 0.1rem;
  overflow: hidden;
}
.movieSearch-result .movieSearch-item .movieSearch-item-rt h3 {
  font-size: 20px;
  font-weight: 500;
  color: #111;
}
.movieSearch-result .movieSearch-item .movieSearch-item-rt .item-rating .score {
  font-size: 12px;
  margin-left: 0.1rem;
}
.movieSearch-result .movieSearch-item .movieSearch-item-rt p {
  color: #aaa;
  line-height: 1.5;
  text-align: justify;
  margin-top: 0.1rem;
  font-size: 12px;
}


