
.newBookMore .content {
  padding-top: 50px;
  background-color: #fff;
}
.newBookMore .content .title {
  width: 100%;
  height: 50px;
  box-sizing: border-box;
  padding: 0 1.5rem;
  background-color: #fcfcfc;
  overflow: hidden;
}
.newBookMore .content .title a {
  float: left;
  display: inline-block;
  width: 2.25rem;
  height: 49px;
  line-height: 50px;
  text-align: center;
  font-size: 16px;
  color: #888;
  border-bottom: 1px solid #ddd;
}
.newBookMore .content .title a.active {
  color: #42bd56;
  border-bottom: 2px solid #42bd56;
}
.newBookMore .content .contentList {
  padding: 0.25rem;
}


